.anniversary-box {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  background-image: url('./img/bg.png');
  background-size: 100% 100%;
  background-repeat: no-repeat;

  .color-text {
    background: linear-gradient(180deg, #5A82F6 0%, rgba(255, 255, 255, 0) 100%), linear-gradient(91.34deg, #2698EA 11.48%, #FFB176 53.97%, #ED5158 100.98%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-transform: uppercase;
  }

  .anniversary-illustration {
    width: 90%;
    // padding-left: 150px;
    height: 787px;
    position: relative;

    .illustration-box {
      display: flex;
      position: relative;

      .illustration-left {
        max-width: 643px;
        font-size: 32px;
        margin-top: 80px;

        .anniversary-title {
          font-size: 72px;
          color: #FFFFFF;
          margin-top: 132px;
        }

        .rewards-text {
          font-weight: 400;
          color: #EBEBEB;
        }

        .trade-type {
          display: flex;
          align-items: center;
          font-weight: 700;
          font-size: 36px;
          color: #FFFFFF;
          margin-top: 24px;

          span {
            margin-left: 20px;
          }

          span:first-child {
            margin-left: 0;
          }

          .option-type {
            border-left: 1px solid #EBEBEB;
            border-right: 1px solid #EBEBEB;
            padding: 0 20px;
            height: 30px;
            display: flex;
            align-items: center;
          }
          .powers-type{
            border-right: 1px solid #EBEBEB;
            padding: 0 20px 0 0;
            height: 30px;
            display: flex;
            align-items: center;
          }

        }
      }

      .illustration-right {
        width: 100%;
        height: 728px;
        position: relative;
        margin-top: 20px;

        img {
          width: 738px;
          height: 728px;
          position: absolute;
          right: 80px;
        }
      }
    }

    .connect-wallet-box {
      width: 100%;
      height: 140px;
      font-weight: 400;
      font-size: 32px;
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      bottom: 0;
      z-index: 1;
      color: #EBEBEB;
      background: linear-gradient(180deg, #5A82F6 0%, rgba(255, 255, 255, 0) 100%),
        linear-gradient(91.34deg, #2698EA 11.48%, #FFB176 53.97%, #ED5158 100.98%);
      border-radius: 8px;

      &-conent {
        width: calc(100% - 4px);
        height: 136px;
        padding: 0 56px;
        display: flex;
        background: #233049;
        align-items: center;
        justify-content: space-between;
        border-radius: 8px;
      }

      .acc-wrapper {
        background-color: rgba(85, 119, 253, 0.1);
        border: 1px solid #364F70;
        border-radius: 8px;
        width: 405px;
        padding: 0 8px;
        height: 60px;
        display: flex;
        justify-content: center;
        align-items: center;

        img {
          margin-right: 8px;
        }
      }
    }



  }

  .anniversary-activity {
    margin-top: 180px;
    margin-bottom: 40px;

    .anniversary-activity-title {
      font-size: 72px;
      margin-bottom: 102px;
      text-align: center;
    }

    .anniversary-activity-info {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      flex-wrap: wrap;
      width: 1372px;

      .activity-card-info {
        width: 430px;
        height: 704px;
        margin-top: 45px;
        display: flex;
        justify-content: center;
        backdrop-filter: blur(25px);
        margin-left: 40px;
        align-items: center;
        background: linear-gradient(221.3deg, #FFEB00 -11.84%, #FDBD00 -0.59%, #FE9A00 10.66%, #FF5200 21.91%, #FF2200 33.17%, #A9134A 44.42%, #8700B2 55.67%, #3D00A7 66.92%, #003DFF 78.17%, #0091D0 89.43%, #62B224 100.68%);
        border-radius: 20px;

        &-box {
          width: 426px;
          height: 700px;
          background: #0e162e;
          border-radius: 20px;
          padding: 21px 21px 32px 21px;

          &-header {
            display: flex;
            justify-content: space-between;
            align-items: center;
            font-size: 32px;
            height: 47px;

            .timeout {
              width: 193px;
              height: 47px;
              font-weight: 500;
              font-size: 20px;
              border-radius: 133.984px;
              color: #EBEBEB;
              display: flex;
              align-items: center;
              justify-content: center;
              background: rgba(255, 255, 255, 0.2);
              border: 1.33984px solid rgba(255, 255, 255, 0.6);
            }
          }

          &-img {
            width: 388px;
            height: 388px;
            margin-top: 22px;
            background-color: #0c1637;
            border-radius: 20px;
            overflow: hidden;

            img {
              width: 100%;
              height: 100%;
            }
          }

          &-text {
            font-weight: 700;
            font-size: 20px;
            height: 48px;
            color: #FFFFFF;
            margin-top: 22px;
          }

          &-button {
            margin-top: 22px;
            display: flex;
            align-items: center;
            justify-content: center;

            .button-more {
              width: 388px;
              height: 64px;
              background: #5577FD;
              border-radius: 133.984px;
              font-weight: 700;
              font-size: 21.4375px;
            }

            .button-more:hover {
              background: linear-gradient(180deg, #5A82F6 4.68%, rgba(90, 130, 246, 0) 100.15%), linear-gradient(90deg, #2698EA 0.03%, #FFB176 46.84%, #ED5158 99.89%);
              border: 2px solid #FFFFFF;
            }
          }
        }
      }

      .activity-card-info:nth-child(3n+1) {
        margin-left: 0;
      }

      .isStart-activity {
        background: #7D7D7D;
        backdrop-filter: blur(25px);
        position: relative;

        .has-not-started {
          position: absolute;
          width: 100%;
          height: 81%;
          z-index: 1;
          top: 0;
          left: 0;
          background: linear-gradient(176.82deg, rgba(23, 22, 47, 0.3) -3.59%, rgba(13, 12, 30, 0.3) 140.07%);
        }

        .activity-card-info-box-button {
          .button-more {
            border: 2px solid #7D7D7D;
            background-color: transparent;
          }
        }

      }
    }
  }
}


@media only screen and (max-width: 1372px) {
  .anniversary-box {
    .anniversary-illustration {
      .illustration-box {
        .illustration-right {
          img {
            width: 514px;
            height: 772px;
          }
        }
      }
    }

    .anniversary-activity {
      .anniversary-activity-info {
        width: 940px;
        margin: 0 auto;

        // .activity-card-info:nth-child(2n+1){
        //   margin-left: 0;          
        // }
        .activity-card-info:nth-child(3n+1) {
          margin-left: 40px;
        }
      }
    }
  }
}

@media only screen and (max-width: 1140px) {
  .anniversary-box {
    background-image: url('./img/bg-m.png');

    .anniversary-illustration {
      height: 100%;

      .illustration-box {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 325px;
        margin: 0 auto;

        .illustration-left {
          font-size: 28px;
          width: 325px;

          .anniversary-title {
            font-size: 40px;
            margin-top: 12px;
          }

          .trade-type {
            font-size: 24px;
          }
        }

        .illustration-right {
          display: flex;
          align-items: center;
          justify-content: center;
          margin-top: 0;
          overflow: hidden;
          height: 620px;

          img {
            position: static;
            width: 547px;
            height: 591px;
          }
        }
      }

      .connect-wallet-box {
        font-size: 14px;
        height: 97px;

        .connect-wallet-box-conent {
          background: #354295;
          flex-direction: column;
          height: 93px;
          justify-content: center;
          .m-connect{
            width: 132px;
            height: 24px;
            font-size: 12px;
            background-color: #5577FD;
            border: 0;
            margin-top: 8px;
          }
          .acc-wrapper {
            width: 132px;
            height: 24px;
            border-radius: 4px;
            margin-top: 8px;
            font-size: 12px;
            img{
              width: 20px;
            }
          }
        }
      }
    }
    .anniversary-activity{
      margin-top: 79px;
      .anniversary-activity-title{
        font-size: 40px;
        margin-bottom: 87px;
        width: 325px;
      }
      .anniversary-activity-info{
        width: 328px;
        .activity-card-info{
          width: 328px;
          height: 528px;
          margin-left: 0 !important;
          .activity-card-info-box{
            width: 324px;
            height: 524px;
            padding: 15px 15px 22px 15px;
            .activity-card-info-box-header{
              font-size: 26px;
              .timeout{
                width: 145px;
                height: 35px;
                font-size: 18px;
              }
            }
            .activity-card-info-box-img{
              width: 294px;
              height: 294px;
            }
            .activity-card-info-box-text{
              font-size: 16px;
              margin-top: 14px;
            }
            .activity-card-info-box-button{
              .button-more{
                width: 298px;
                height: 48px;
                font-size: 18px;
              }
            }
          }
        }
      }
    }
  }
}